<template>
	<div class="page">
		<div class="page-content">
			<div class="page-content-detail-hospital">
				<div class="detail-info">
					<h1 class="name">{{sourceDate.name}}</h1>
					
					<div>
						<img :src="sourceDate.headimg">
					</div>

					<p v-for="item in sourceDate.content">{{item}}</p>
				</div>

				<DetailFooter :data="detailFooter" />
			</div>
		</div>
	</div>
</template>

<script>
	import CompanyBar from './component/company-bar.vue'
	import DetailFooter from './component/detail-footer.vue'

	export default {
		components: { CompanyBar, DetailFooter },
		data() {
			return {
				sourceDate: {},
				detailFooter: {
					content: '更多合作医院'
				}
			}
		},
		mounted() {
			if (sessionStorage.hospital) {
				let _hospital = JSON.parse(sessionStorage.hospital);

				_hospital.map(item => {
					if (item.id === this.$route.query.id) {
						this.sourceDate = item;
					}
				})
			}
		},
		methods: {
			
		}
	}
</script>

<style lang="scss" scoped>
	.page-content-detail-hospital {
		h1 {
			font-size: 22px;
			color: #222;
		}

		h3 {
			font-size: 14px;
			color: #222;
		}

		.tag {
			color: #666;
			font-size: 12px;
			margin-bottom: 16px;
		}

		p {
			font-size: 14px;
			color: #333;
		}

		.detail-info {
			background: #fff;
			padding: 20px 16px;
			margin-bottom: 8px;

			div {
				text-align: center;
				margin-bottom: 20px;

				img {
					width: 100%;
				}

				span {
					color: #666;
					font-size: 12px;
				}
			}
		}
	}
</style>


