import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

// Page Components
import Index from './components/Index.vue'
import Home from './components/Home.vue'
import About from './components/About.vue'
import Consult from './components/Consult.vue'
import Hospital from './components/Hospital.vue'
import Item from './components/Item.vue'
import Detail from './components/Detail.vue'
import DetailHospital from './components/Detail-hospital.vue'
import DetailItem from './components/Detail-item.vue'
import DetailServer from './components/Detail-server.vue'
import Tabbar from './components/component/Tabbar.vue'

// Routes
const routes = [
  { path: '/', component: Index },
  { path: '/home', component: Home, meta: { title: '泰璞健康' } },
  { path: '/about', component: About, meta: { title: '关于我们' } },
  { path: '/consult', component: Consult, meta: { title: '咨询' } },
  { path: '/hospital', component: Hospital, meta: { title: '合作医院' } },
  { path: '/item', component: Item, meta: { title: 'Item' } },
  { path: '/tabbar', component: Tabbar },
  { path: '/detail', component: Detail },
  { path: '/detail-hospital', component: DetailHospital },
  { path: '/detail-item', component: DetailItem },
  { path: '/detail-server', component: DetailServer },
]


