<template>
	<div class="page">
		<div class="page-content">
			<div class="page-content-body">
				<Home @returnMenuRes="returnRes" v-if="activeType == 'Home'" />
				<Server :from="from" v-if="activeType == 'Server'" />
				<About v-if="activeType == 'About'" />
				<Consult :from="from" v-if="activeType == 'Consult'" />
				<Hospital v-if="activeType == 'Hospital'" />
				<Item v-if="activeType == 'Item'" />
				<DetailServer :type="activeType" @returnMenuRes="returnRes" v-if="activeType == 'Experience' || activeType == 'Purify' || activeType == 'Antifading'" />
			</div>

			<Tabbar :type="activeType" @returnMenuRes="returnRes" />

			<Menus :show="showMenu" @returnMenuRes="returnRes" />
		</div>
	</div>
</template>

<script>
	import Home from './Home.vue'
	import Server from './Server.vue'
	import About from './About.vue'
	import Consult from './Consult.vue'
	import Hospital from './Hospital.vue'
	import Item from './Item.vue'
	import Tabbar from './component/Tabbar.vue'
	import Menus from './Menu.vue'
	import DetailServer from './Detail-server.vue'

	export default {
		components: { Home, Server, About, Consult, Hospital, Item, Tabbar, Menus, DetailServer },
		data () {
			return {
				activeType: 'Home',
				from: '',
				showMenu: false,
			}
		},
		mounted() {
			if (sessionStorage.activeType) this.activeType = sessionStorage.activeType;
		},
		methods: {
			returnRes(res) {
				this.showMenu = res.show;
				this.from = res.from;

				switch(res.activeType) {
					case 'Menu':
						this.showMenu = true;
						this.activeType = res.activeType;
						break;
					default:
						this.activeType = res.activeType;
						sessionStorage.activeType = res.activeType;
				}
			}
		},
		watch: {
			activeType: {
				handler(newValue, oldValue) {
					console.log(1111);
					document.title = newValue;
				}
			}
		}
	}
</script>

<style lang='scss' scoped>
	.page-content-body {
		height: calc(100% - 60px);
		overflow-y: auto;
	}
</style>



