<template>
	<div class="page-content-consult">
		<div class="items">
			<a href="tel:+400-282-9900" class="consult-item">
				<div class="title"><i class="icon ion-phone"></i> 电话咨询</div>
				<div class="phone">400-282-9900</div>
				<div class="tips">点击直接拨打电话</div>
			</a>
		</div>

		<div class="items" @click="onLineConsult()">
			<div class="consult-item">
				<div class="title"><i class="icon ion-consult"></i>在线客服</div>
				<div class="tips">24小时在线客服为您服务</div>
			</div>
		</div>

		<div class="items" v-if="from == 'Menu'" @click="onLineJiameng()">
			<a href="tel:+400-282-9900" class="consult-item">
				<div class="title"><i class="icon ion-jiameng"></i>招商加盟</div>
				<div class="tips">我们诚邀您加入泰璞健康</div>
			</a>
		</div>

		<div class="alert" v-if="showTel">
			<div class="alert-wapper">
				<div class="title">{{alertItem.title}}</div>
				<div class="content">{{alertItem.content}}</div>
				<div class="button-group" :class="{'button-group-2': alertItem.button.length == 2}">
					<a href="javascript:;" class="cancel" @click="closeAlert()" v-if="alertItem.button.length==1">{{alertItem.button[0]}}</a>
					<a href="javascript:;" class="confrim" @click="alertComfrim()">{{alertItem.button[1]}}</a>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['from'],
		data() {
			return {
				showTel: false,
				alertItem: {
					title: '',
					content: '',
					button: []
				}
			}
		},
		mounted() {
			
		},
		methods: {
			onLineConsult() {
				console.log('调用微信回话系统');
			},
			onLineTel() {
				this.alertItem = {
					title: '提示',
					content: '确定拨打 400-282-9900 吗？',
					button: [ '取消', '确定' ],
				};
				this.showTel = true;
			},
			onLineConsult() {
				this.alertItem = {
					title: '您可以在微信公众号聊天界面中留言，我们会第一时间回复您',
					button: [ '知道了' ]
				};
				this.showTel = true;
			},
			closeAlert() {
				this.showTel = false;
			},
			alertComfrim() {
				this.showTel = false;
			}
		}
	}
</script>

<style lang='scss' scoped>
	.page-content-consult {
		height: 100%;
		padding: 16px;
		background: #fff;

		.items {
			display: table;
			width: 100%;
			margin-bottom: 16px;
			border: 1px solid #f2f2f2;
			box-shadow: 0 2px 2px rgba(0,0,0,.04);

			.consult-item {
				width: 100%;
				height: 120px;
				margin-bottom: 16px;
				background: #fff;
				text-align: center;
				display: table-cell;
				vertical-align: middle;

				.title {
					color: #333;
					font-size: 16px;
					font-weight: 700;
					position: relative;
				}

				.phone {
					color: #00A9D6;
					font-size: 24px;
					margin: 7px 0;
				}

				.tips {
					color: #999;
					font-size: 12px;
				}

				i {
					display: block;
					position: absolute;
					top: 2px;
					left: calc(50% - 50px);
					width: 15px;
					height: 15px;

					&.ion-phone {
						background: url(../img/common/icon_phone.png);
						background-size: 100%;
					}

					&.ion-consult {
						background: url(../img/common/icon_info.png);
						background-size: 100%;
					}

					&.ion-jiameng {
						background: url(../img/common/icon_jiameng.png);
						background-size: 100%;
					}
				}
			}
		}

		.popup-title {
			padding: 20px;
		}

		.alert {
			position: absolute;
			z-index: 10;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: rgba(0,0,0,.4);

			.alert-wapper {
				width: 270px;
				background: #fff;
				border-radius: 10px;
				text-align: center;
				transform: translate3d(0, 100%, 0);
				margin: auto;

				.title {
					font-size: 17px;
					color: #030303;
					font-weight: 700;
					padding: 20px 0 0;
				}

				.content {
					margin-top: 20px;
				}

				.button-group {
					line-height: 45px;
					overflow: hidden;
					border-top: 1px solid #eee;
					margin-top: 20px;

					a {
						display: inline-block;
						width: 100%;
						height: 100%;
						float: left;
						color: #007AFF;
						font-weight: 700;
						position: relative;

						&:after, &:before {
							position: absolute;
							top: 0;
							width: .5px;
							height: 100%;
							background: #eee;
						}

						&.cancel:after {
							content: "";
							display: block;
							right: 0;
						}

						&.confrim:before {
							content: "";
							display: block;
							left: 0;
						}
					}
				}

				.button-group-2 {
					a {
						width: 50%;
					}
				}
			}
		}
	}

	a {
		text-decoration: none;
	}
</style>



