		<p>体检越来越受到人们的重视，现在很多人每年都定期体检，在国内很多人也定期体检，尤其是富人，但是很多人在体检后还是会在短时期内查出癌症。而且国内体检也被爆出作假，如血液不化验直接出结果，这样很多人把体检瞄准到国外。日本体检做的非常好，尤其是防癌体检，在国际上是首屈一指的，那么日本防癌体检和一般体检有什么不同之处？</p>
		<p>日本精密体检是指以不管有无自觉症状,定期地通过CT，MRT(核磁共振),超声波等紧密仪器,对身体各部位就行的一种精密检查，主要目的是为了检查癌症，其次是各种生活习惯病。它是一项对自我近期健康对检测的重要标准。</p>

