		<div>
			<h3><span></span> 建议进行日本精密体检人群</h3>

			<div class="info">
				<h4>家族有癌患者人群</h4>
				<p>研究发现，癌症是个体遗传基因变异和致癌物质相互作用导致的。有些肿瘤有家族聚集性和遗传易感性，对于有家族癌症史的人来说，一定要积极进行预防肿瘤的定期检查，及时发现</p>
			</div>

			<div class="info">
				<h4>中老年人群</h4>
				<p>症在各个年龄段都有发生，但发病高峰在50岁以上中老年人群中，应定期体检来早期发现肿瘤。</p>
			</div>

			<div class="info">
				<h4>常接触致癌物质人群</h4>
