<template>
	<div class="page-content-server">
		<CompanyBar />
		<CompanyHeader :companySwiper="companySwiper" />

		<div class="content-info">
			<!-- <div class="tabs-striped">
				<span class="tabs-item" :class="{'tabs-item-active': tabIndex == index}" v-for="(item, index) in tabs" @click="onTabClick(index)">{{item}}</span>
			</div> -->

			<div class="tabs-striped-content">
				<scroller class="scroller"
					:on-refresh="onRefresh"
					:on-infinite="onInfinite"
					ref="scroller">
					<router-link tag="div" class="tabs-striped-content-item" :to="{'path': '/detail', query: { id: item.id }}" v-for="item in items">
						<img :src="item.headimg">

						<div class="img-info">
							<h3>{{item.name}}</h3>
							<div class="item-info">{{item.content}}</div>
							<p>{{item.dateTime}}</p>
						</div>
					</router-link>
				</scroller>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	import _ from 'lodash'
	import CompanyBar from './component/company-bar.vue'
	import CompanyHeader from './component/company-header.vue'

	// const data0 data1 data2 为模拟数据，接入借口后需要删除
	const data0 = {
		"code": 0,
		"data": {
			"list": [
				{
					"id": 3,
					"headimg": "./static/img/server/tab_item_3.png",
					"name": "尝试马云在东京做的体检，…",
					"content": "身为国内中坚民企的老总，我几乎把所有的精力都放在了工作上。企业…",
					"dateTime": "2017.12.19"
				},{
					"id": 1,
					"headimg": "./static/img/server/tab_item_1.png",
					"name": "为什么商界强人选择双重血...",
					"content": "今年50岁的张女士（化名），是一位实打实的女强人。工作上作为公司…",
					"dateTime": "2017.12.19"
				},{
					"id": 2,
					"headimg": "./static/img/server/tab_item_2.png",
					"name": "中国贵宾在日本东京表参道...",
					"content": "自从在日本做了干细胞美容之后，王女士不仅肤嫩脸润，更好像变…",
					"dateTime": "2018.08.27"
				}
			],
			page: 1,
			next: true
		}
	}

	export default {
		props: ['from'],
		components: { CompanyBar, CompanyHeader },
		data() {
			return {
				companySwiper: {
					titleName: '服务案例',
					titleInfo: '权威、专业、全面的医疗咨询服务',
					className: 'server'
				},
				tabs: ["精密体验", "血液净化", "干细胞抗衰"],
				tabIndex: 0,
				items: [],
			}
		},
		mounted() {
			fnTiyan(this);
			// if (this.tabIndex == 0) fnTiyan(this);

			// switch(this.from) {
			// 	case 'Experience':
			// 		this.tabIndex = 0;
			// 		fnTiyan(this);
			// 		break;
			// 	case 'Purify':
			// 		this.tabIndex = 1;
			// 		fnJinghua(this);
			// 		break;
			// 	case 'Antifading':
			// 		this.tabIndex = 2;
			// 		fnKangshuai(this);
			// 		break;
			// }
		},
		methods: {
			onTabClick(index) {
				this.tabIndex = index;

				switch(index) {
					case 0:
						fnTiyan(this);
						break;
					case 1:
						fnJinghua(this);
						break;
					case 2:
						fnKangshuai(this);
						break;
				}
			},

			onRefresh() {
				// 下拉刷新, 需要根据当前 tabIndex
				setTimeout(() => {
					if (this.$refs.scroller) this.$refs.scroller.finishPullToRefresh();
				})
			},
			onInfinite() {
				// 到底部时加载新数据, 需要根据当前 tabIndex
				fnTiyan(this);

				// switch(this.tabIndex) {
				// 	case 0:
				// 		fnTiyan(this);
				// 		break;
				// 	case 1:
				// 		fnJinghua(this);
				// 		break;
				// 	case 2:
				// 		fnKangshuai(this);
				// 		break;
				// }
			}
		}
	}

	function fnTiyan(that, params) {
		let response = data0;
		// let _page = response.page;
		
		// if (response.next) _page = _page + 1;

		// let _params = {
		// 	page: _page
		// }

		if (response.code === 0) {
			that.items = response.data.list;
			// that.items = _.concat(that.items, response.data.list);

			setTimeout(() => {
				if (that.$refs.scroller) that.$refs.scroller.finishInfinite();
			})
		}

		// axios.get(
		// 	that.$api.tiyan + qs.stringify(_params),
		// 	{
		// 		headers: {
		// 			'Content-Type': 'application/x-www-form-urlencoded'
		// 		},
		// 		timeout: 300000
		// 	}
		// ).then(response => {
		// 	console.log(response);
		// 	that.items = _.concat(that.items, response.data.list);
		// }).catch(error => {
		// 	if (error.response && error.response.data && error.response.data.code) {
		// 		alert(error.response.data.msg)
		// 	}
		// });
	}

	function fnJinghua(that, params) {
		let response = data1;
		if (response.code === 0) {
			that.items = response.data.list;
		}
	}

	function fnKangshuai(that, params) {
		let response = data2;
		if (response.code === 0) {
			that.items = response.data.list;
		}
	}
</script>

<style lang='scss' scoped>
	.page-content-server {
		height: 100%;

		.content-info {
			height: calc(100% - 160px);
			overflow: hidden;

			.tabs-striped {
				overflow: hidden;
				background: #fff;
				box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);

				span {
					display: inline-block;
					float: left;
					width: calc(100%/3);
					line-height: 40px;
					text-align: center;
					position: relative;

					&.tabs-item-active:after {
						content: "";
						display: block;
						position: absolute;
						left: calc(50% - 14px);
						bottom: 0;
						width: 28px;
						height: 2px;
						border-radius: 10px;
						background: #00A9D6;
						transition: all .3s;
					}
				}
			}

			.tabs-striped-content {
				height: 100%;
				background: #fff;
				padding: 0 16px;
				overflow: hidden;
				position: relative;
				margin-top: 8px;

				.scroller {
					height: 100%;
					padding: 0 16px;

					&>div {
						height: 100%;
					}
				}

				.tabs-striped-content-item {
					padding: 20px 0;
					border-bottom: .5px solid #eee;
					overflow: hidden;

					img {
						width: 120px;
						float: left;
					}

					h3 {
						color: #333;
						font-size: 16px;
						margin-bottom: 5px;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
						font-weight: 700;
					}

					.img-info {
						color: #999;
						width: calc(100% - 122px);
						float: left;
						padding-left: 12px;

						.item-info {
							font-size: 13px;
							margin-bottom: 0;
							display: -webkit-box;
							-webkit-box-orient: vertical;
							-webkit-line-clamp: 2;
							overflow: hidden;
							/* autoprefixer: off */
							-webkit-box-orient: vertical;
							/* autoprefixer: on */
						}

						p {
							font-size: 12px;
							float: left;
							margin-bottom: 0;
						}
					}
				}
			}

			.tabs-striped-content-header {
				height: calc(100% - 48px);
			}
		}
	}
</style>


