	}
</script>

<style lang='scss' scoped>
	.company-bar {
		height: 40px;
		overflow: hidden;
	}
	
	.content-swiper {
		height: 120px;
		padding: 36px 22px;
		color: #fff;

		h3 {
			font-size: 18px;
			color: #fff;
			margin-bottom: 8px;
		}

		span {
			font-size: 14px;
		}

		&.content-swiper-server {
			background: url('../../img/server/company_swiper_bg.png');
			background-size: 100%;
		}

		&.content-swiper-about {
			background: url('../../img/about/company_swiper_bg.png');
			background-size: 100%;
		}

		&.content-swiper-hospital {
			background: url('../../img/hospital/company_swiper_bg.png');
			background-size: 100%;
		}

		&.content-swiper-item {
			background: url('../../img/item/company_swiper_bg.png');
			background-size: 100%;
		}
	}
</style>


