		<ul>
			<li>根据体检报告和个人意愿确定治疗方向</li>
			<li>院方审核材料，确定收治和方案</li>
			<li>签署服务合同，支付定金</li>
			<li>准备签证资料，获得签证后，付清全额费用，与日本医疗机构做确认预约</li>
			<li>阅读赴日治疗须知，做好出发前准备</li>
			<li>根据预定航班提前到达出发前城市</li>
			<li>前往日本开始健康之旅</li>
			<li>回国</li>
		</ul>
	</div>
</template>

<script>
	export default {
		props: [],
		data() {
			return {
				
			}
		},
		methods: {
			back() {
				$router.back('/')
			}
		}
	}
</script>

