<template>
	<div class="page">
		<div class="page-content">
			<div class="page-content-detail-server">
				<DetailTiyan v-if="activeType == 'Experience'" />
				<DetailJinghua v-if="activeType == 'Purify'" />
				<DetailGanxibao v-if="activeType == 'Antifading'" />

				<DetailSetmeal />
				<DetailServerProcess />
				<DetailServer @returnMenuRes="returnRes" />
				<DetailAbout />
				<DetailServerOther :activeType="activeType" @returnMenuRes="returnRes" />
				<CompanyInfo />
			</div>

			<Tabbar @returnMenuRes="returnRes" />
		</div>
	</div>
</template>

<script>
	import DetailTiyan from './servers/detail-tiyan.vue'
	import DetailJinghua from './servers/detail-jinghua.vue'
	import DetailGanxibao from './servers/detail-ganxibao.vue'

	import DetailSetmeal from './component/detail-setmeal.vue'
	import DetailServerProcess from './component/detail-server-process.vue'
	import DetailServer from './component/detail-server.vue'
	import DetailAbout from './component/detail-about.vue'
	import DetailServerOther from './component/detail-server-other.vue'
	import CompanyInfo from './component/company-info.vue'

	import Tabbar from './component/Tabbar.vue'

	export default {
		props: ['type'],
		components: { DetailTiyan, DetailJinghua, DetailGanxibao, DetailSetmeal, DetailServerProcess, CompanyInfo, DetailAbout, DetailServerOther, DetailServer, Tabbar },
		data() {
			return {
				sourceDate: {},
				activeType: '',
			}
		},
		mounted() {
			console.log(this.type)
			this.activeType = sessionStorage.activeType || this.type;
		},
		methods: {
			returnRes(res) {
				if (res.activeType == 'Experience' || res.activeType == 'Purify' || res.activeType == 'Antifading') {
					document.querySelector('.page-content-detail-server').scrollTop = 0;
				}

				this.activeType = res.activeType;
				sessionStorage.activeType = res.activeType;

				console.log(this.activeType)

				this.$emit('returnMenuRes', {
					activeType: res.activeType,
					from: res.from
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	.page-content-detail-server {
		height: calc(100% - 60px);
		overflow-y: auto;
	}
</style>


