<template>
	<div class="detail-server-other">
		<h3 class="title">其他服务项目</h3>

		<div class="items-project">
			<div class="items-project-item project-item-1" v-if="activeType !== 'Experience'" @click="changeTabbar('Experience')">
				<div>
					<h3>精密体验</h3>
					<p>赴日本精密体验，提前了解健康隐患</p>
				</div>
			</div>

			<div class="items-project-item project-item-2" v-if="activeType !== 'Purify'" @click="changeTabbar('Purify')">
				<div>
					<h3>血液净化</h3>
					<p>打造强壮体魄，改善健康状况</p>
				</div>
			</div>

			<div class="items-project-item project-item-3" v-if="activeType !== 'Antifading'" @click="changeTabbar('Antifading')">
				<div>
					<h3>干细胞疗法</h3>
					<p>调节防御机能，抵御疾病，延缓衰老</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['activeType'],
		data() {
			return {
				returnRes: {}
			}
		},
		methods: {
			changeTabbar(type) {
				this.returnRes.activeType = type;
				this.returnRes.from = '';
				this.$emit('returnMenuRes', this.returnRes);
			},
			back() {
				$router.back('/')
			}
		}
	}
</script>

<style lang='scss' scoped>
	.detail-server-other {
		margin: 8px 0;
		background: #fff;

		h3.title {
			height: 50px;
			line-height: 50px;
			font-size: 18px;
			font-weight: 700;
			text-align: center;
			border-bottom: 1px solid #eee;
			margin-bottom: 0;
		}

		.items-project-item {
			height: 120px;
			font-size: 14px;
			color: #fff;
			display: table;
			width: 100%;
			margin-bottom: 1px;
			

			>div {
				text-align: center;
				display: table-cell;
				vertical-align: middle;
			}

			h3 {
				font-size: 18px;
				color: #fff;
			}

			&.project-item-1 {
				background: url(../../img/index/index_project_1.png);
				background-size: 100%;
			}

			&.project-item-2 {
				background: url(../../img/index/index_project_2.png);
				background-size: 100%;
			}

			&.project-item-3 {
				background: url(../../img/index/index_project_3.png);
				background-size: 100%;
			}
		}
	}
</style>


