<template>
	<div class="page">
		<div class="page-content">
			<div class="page-content-detail-item">
				<CompanyBar />
				<div class="company-bar"></div>

				<div class="detail-infos">
					<div class="item-info">
						<img :src="sourceDate.headimg">

						<div class="info">
							<h3 class="name">{{sourceDate.name}}</h3>
							<p>{{sourceDate.hospital}}</p>
							<p>{{sourceDate.position}}</p>
						</div>
					</div>

					<div class="detail">
						<div class="detail-info">
							<div v-if="sourceDate.field">
								<h4>专业领域</h4>
								<p>{{sourceDate.field}}</p>
							</div>

							<div v-if="sourceDate.introduce">
								<h4>介绍</h4>
								<p>{{sourceDate.introduce}}</p>
							</div>

							<div v-if="sourceDate.record && sourceDate.record.length>0">
								<h4>医师履历</h4>
								<p v-for="item in sourceDate.record">{{item}}</p>
							</div>

							<div v-if="sourceDate.Authentication && sourceDate.Authentication.length>0">
								<h4>资格认证</h4>
								<p v-for="item in sourceDate.Authentication">{{item}}</p>
							</div>

							<div v-if="sourceDate.supplement && sourceDate.supplement.length>0">
								<h4>资格认证</h4>
								<p v-for="item in sourceDate.supplement">{{item}}</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import CompanyBar from './component/company-bar.vue'

	export default {
		components: { CompanyBar },
		data() {
			return {
				sourceDate: {}
			}
		},
		mounted() {
			if (sessionStorage.expert) {
				this.sourceDate = JSON.parse(sessionStorage.expert)[this.$route.query.type];
			}
		},
		methods: {
			
		}
	}
</script>

<style lang="scss" scoped>
	.page-content-detail-item {
		height: 100%;
		background: #fff;

		.company-bar {
			height: 40px;
			overflow: hidden;
		}

		.detail-infos {
			background: #fff;

			.item-info {
				padding: 20px 16px;
				overflow: hidden;

				img {
					width: 122px;
					height: 122px;
					border-radius: 100%;
					float: left;
					margin-right: 16px;
				}

				h3.name {
					font-size: 16px;
					color: #333;
					margin: 25px 0 4px 0;
				}

				p {
					font-size: 14px;
					color: #666;
					margin-bottom: 3px;
				}
			}

			.detail {
				padding: 0 16px;
				overflow: hidden;

				.detail-info{
					border-top: 1px solid #ddd;

					h4 {
						font-size: 14px;
						color: #333;
						margin-top: 30px;
						font-weight: 700;
					}

					p {
						color: #666;
					}
				}
			}
		}
	}
</style>


