<template>
	<div class="page-content-tabbar tabbar">
		<div class="tabbar-item" :class="{'tabbar-item-active': returnRes.activeType == 'Menu'}" @click="changeTabbar('Menu')">
			<div class="icon-wrapper">
				<i class="icon ion-menu"></i>
			</div>
			<div class="text-wrapper">
				<span>导航</span>
			</div>
		</div>
		
		<div class="tabbar-item" :class="{'tabbar-item-active': returnRes.activeType == 'Server'}" @click="changeTabbar('Server')">
			<div class="icon-wrapper">
				<i class="icon ion-server"></i>
			</div>
			<div class="text-wrapper">
				<span>服务案例</span>
			</div>
		</div>

		<div class="tabbar-item" :class="{'tabbar-item-active': returnRes.activeType == 'About'}" @click="changeTabbar('About')">
			<div class="icon-wrapper">
				<i class="icon ion-about"></i>
			</div>
			<div class="text-wrapper">
				<span>关于我们</span>
			</div>
		</div>

		<div class="tabbar-item" :class="{'tabbar-item-active': returnRes.activeType == 'Consult'}" @click="changeTabbar('Consult')">
			<div class="icon-wrapper">
				<i class="icon ion-consult"></i>
			</div>
			<div class="text-wrapper">
				<span>咨询</span>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['type'],
		data() {
			return {
				returnRes: {
					activeType: 'Home'
				}
			}
		},
		mounted() {
			if (this.type) this.returnRes.activeType = this.type;
		},
		methods: {
			changeTabbar(type) {
				this.returnRes.activeType = type;
				this.returnRes.from = 'Tabbar';
				this.$emit('returnMenuRes', this.returnRes);
			}
		},
		watch: {
			type: {
				handler(newValue, oldValue) {
					this.returnRes.activeType = newValue;
				}
			}
		}
	}
</script>

<style lang='scss' scoped>
	.page-content-tabbar {
		height: 60px;
		background: #fff;
		border-top: 0.5px solid #eee;
		opacity: 1;
		box-shadow: none;

		span {
			font-size: 14px;
			color: #8799a0;
		}

		i {
			display: block;
			width: 24px;
			height: 24px;
			margin: 7px auto 0;

			&.ion-menu {
				background: url(../../img/common/icon_tabbar_menu.png);
				background-size: 100%;
			}
			&.ion-server {
				background: url(../../img/common/icon_tabbar_server.png);
				background-size: 100%;
			}
			&.ion-about {
				background: url(../../img/common/icon_tabbar_about.png);
				background-size: 100%;
			}
			&.ion-consult {
				background: url(../../img/common/icon_tabbar_consult.png);
				background-size: 100%;
			}
		}

		.tabbar-item-active {
			i {
				&.ion-menu {
					background: url(../../img/common/icon_tabbar_menu_active.png);
					background-size: 100%;
				}
				&.ion-server {
					background: url(../../img/common/icon_tabbar_server_active.png);
					background-size: 100%;
				}
				&.ion-about {
					background: url(../../img/common/icon_tabbar_about_active.png);
					background-size: 100%;
				}
				&.ion-consult {
					background: url(../../img/common/icon_tabbar_consult_active.png);
					background-size: 100%;
				}
			}

			span {
				color: #00A9D6;
			}
		}
	}
</style>



