<template>
	<div class="detail-server">
		<h3>服务案例</h3>

		<div class="tabs-striped-content-items">
			<router-link tag="div" class="tabs-striped-content-item" :to="{'path': '/detail', query: { id: item.id }}" v-for="(item, index) in sourceDate">
				<img :src="item.headimg">

				<div class="img-info">
					<h2>{{item.name}}</h2>
					<div class="item-info">{{item.content}}</div>
					<span>{{item.dateTime}}</span>
				</div>
			</router-link>
		</div>

		<div class="server-more" @click="changeTabbar('Server')">
			查看更多 <i class="icon inon-arrow-right"></i>
		</div>
	</div>
</template>

<script>
	export default {
		props: [],
		data() {
			return {
				sourceDate: [
					{
						"id": 1,
						"headimg": "./static/img/server/tab_item_1.png",
						"name": "为什么商界强人选择双重血...",
						"content": "今年50岁的张女士（化名），是一位实打实的女强人。工作上作为公司…",
						"dateTime": "2017.12.19"
					},
					{
						"id": 2,
						"headimg": "./static/img/server/tab_item_2.png",
						"name": "中国贵宾在日本东京表参道...",
						"content": "自从在日本做了干细胞美容之后，王女士不仅肤嫩脸润，更好像变…",
						"dateTime": "2018.08.27"
					},
					{
						"id": 3,
						"headimg": "./static/img/server/tab_item_3.png",
						"name": "尝试马云在东京做的体检，…",
						"content": "身为国内中坚民企的老总，我几乎把所有的精力都放在了工作上。企业…",
						"dateTime": "2017.12.19"
					}
				],
				returnRes: {}
			}
		},
		methods: {
			changeTabbar(type) {
				this.returnRes.activeType = type;
				this.returnRes.from = sessionStorage.activeType;
				this.$emit('returnMenuRes', this.returnRes);
			},
			back() {
				$router.back('/')
			}
		}
	}
</script>

<style lang='scss' scoped>
	.detail-server {
		margin: 8px 0;
		background: #fff;

		h3 {
			height: 50px;
			line-height: 50px;
			font-size: 18px;
			font-weight: 700;
			text-align: center;
			border-bottom: 1px solid #eee;
			margin-bottom: 0;
		}

		.tabs-striped-content-items {
			padding: 0 16px;
		}

		.tabs-striped-content-item {
			padding: 16px 0;
			border-bottom: .5px solid #eee;
			overflow: hidden;

			img {
				width: 120px;
				float: left;
			}

			h2 {
				color: #333;
				font-size: 16px;
				margin-bottom: 3px;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}

			.img-info {
				color: #999;
				width: calc(100% - 120px);
				float: left;
				padding-left: 10px;

				.item-info {
					font-size: 13px;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 2;
					overflow: hidden;
					/* autoprefixer: off */
					-webkit-box-orient: vertical;
					/* autoprefixer: on */
				}

				span {
					font-size: 12px;
				}
			}
		}

		.server-more {
			text-align: center;
			line-height: 60px;
			color: #666;

			i.inon-arrow-right {
				display: inline-block;
				width: 12px;
				height: 12px;
				background: url('../../img/common/icon_arrow-right.png') no-repeat;
				background-size: 100%;
				vertical-align: -1px;
			}
		}
	}
</style>


