		<div>
			<h3><span></span> 什么是干细胞</h3>
			<p>我们身体内部的所有细胞，包括构成皮肤或血液等组织器官的细胞，寿命都非常短暂。而为了让 所有的组织能够顺利的更新换代，维持身体良好运作，就必须不断补充新的细胞，来替换即将凋亡的细胞。而拥有这种能力的细胞，就称之为「干细胞」。</p>
			<p>人体中大约有60万亿个细胞，心脏，脑，皮肤等身体组织中细胞较为集中，日常起着非常重要的作用，这些细胞的根元，即起源细胞就是干细胞，干细胞可以分化为各种细胞或组织细胞器官，在组织细胞器官受到损伤等情况下，它可以转化再生出修复组织的细胞。</p>
