			<h3><span></span> 什么是干细胞疗法</h3>
			<p>人体是由一个胚胎干细胞分化成亿万个成体细胞组成的。干细胞是一种能够自我复制和具有分化能力的特殊细胞，具备分化成各种组织器官的潜力。</p>
			<p>干细胞疗法，取人体组织采样，分离出干细胞并在体外进行培育增其数量，重新输回面部、体内，利用干细胞的特殊归巢原理，识别体内老化受损细胞释放的信号，定位到需要修复和新生的部位，定向分化成为该部位细胞，实现对机体功能的整体提升。</p>
