			<p>从血液中直接把导致动脉硬化的脂质和炎症细胞等废物（低密度脂蛋白胆固醇和甘油三酯、炎症物质、病毒等）</p>
			<p>通过去除凝固因子和炎症物质，预防血栓和斑块（预防粘稠血液粘附血管或堵塞血管）</p>
			<p>改善血管内皮功能（在血管壁上像瓦片一样铺满的内皮细胞，会防止胆固醇进入血管壁，迅速溶解形成的血栓，分泌不堵塞血管的物质和，分泌不使血管变窄的血管扩张物质等重要的运作）</p>
			<p>作为血液流动改善效果，糖尿病等引起的足部坏疽，下肢截肢的早提对策，可以在保险适用前的阶段实施</p>
